/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';

export default function Home() {
    const showMenu = () => {
        document.getElementById('navArea').classList.add('custom');
    }

    const closeMenu = () =>{
        document.getElementById('navArea').classList.remove('custom');
    }

    const scrollToTop = () => {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    }

    const toggleNavigationMenu = () => {

        if(document.getElementById('collapse').classList.contains('show')){
          document.getElementById('collapse').classList.remove('show')
          return;
        }
    
        document.getElementById('collapse').classList.add('show');
    }

    const linkAction = () => {
        document.getElementById('navArea').classList.remove('custom')
    }

    return (
        <div className="container-fluid navigation px-md-0" id="nav">
            <div className="container navArea px-0" id="navArea">
                <ul className="navbar-nav">
                    <li className="nav-item mx-1">
                        <a className="nav-link" href="#projects" onClick={linkAction}>Projects</a>
                    </li>
                    <li className="nav-item mx-1">
                        <a className="nav-link" href="#skills" onClick={linkAction}>Skills</a>
                    </li>
                    <li className="nav-item mx-1">
                        <a className="nav-link" href="#devTools" onClick={linkAction}>Dev Tools</a>
                    </li>
                    <li className="nav-item mx-1">
                        <a className="nav-link" href="#contact" onClick={linkAction}>Contact Me</a>
                    </li>
                    <li className="nav-item mx-1">
                        <a className="nav-link" href="#about" onClick={linkAction}>About Me</a>
                    </li>
                </ul>
                <div className="btnClose" onClick={closeMenu}>
                    <div className="icon"></div>
                    <div className="icon"></div>
                </div>
            </div>
            <div className="btnMenu" id="btnMenu" onClick={showMenu}>
                <div className="icon"></div>
                <div className="icon"></div>
                <div className="icon"></div>
            </div>
            <div className="floating_botton">
                <button id="btn_BTT" onClick={scrollToTop}><i className="fa fa-arrow-up"></i></button>
            </div>
            <div className="container px-0 navigation-bar">
                <nav className="navbar navbar-expand-lg navbar-light bg-white">
                    <a className="navbar-brand" href="#">
                        <div className="text-center">
                            <div className="logo">
                                <div className="d-inline"></div>
                                <div className="d-inline"></div>
                                <div className="d-inline"></div>
                                <div className="d-inline"></div> 
                            </div>
                        </div>
                    </a>
                    <button className="navbar-toggler" type="button"aria-label="Toggle navigation" onClick={toggleNavigationMenu}>
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="navbar-collapse collapse navLinks" id="collapse">
                        <ul className="navbar-nav ms-md-auto">
                            <li className="nav-item mx-1">
                                <a className="nav-link" href="#projects">Projects</a>
                            </li>
                            <li className="nav-item mx-1">
                                <a className="nav-link" href="#skills">Skills</a>
                            </li>
                            <li className="nav-item mx-1">
                                <a className="nav-link" href="#devTools">Dev Tools</a>
                            </li>
                            <li className="nav-item mx-1">
                                <a className="nav-link" href="#contact">Contact Me</a>
                            </li>
                            <li className="nav-item mx-1">
                                <a className="nav-link" href="#about">About Me</a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </div>
        </div>
    );
}