import React from 'react';
import Email from 'emailjs-com';

export default function Contact() {
    const sendEmail = (e) => {
        e.preventDefault();

        Email.sendForm(
            'service_dll1uuz', 
            'template_0puafhz', 
            e.target, 
            'user_XOS0WbD0YcO2wsrV563Q9'
        ).then(res => {
            alert("Message was sent to Benj!")
        }).catch(res => {
            if(res.status !== 200){
                alert("Message was not sent!")
            }
        })
    }

    return (
        <div className="contact_section pt-5 pb-5 mt-md-5 px-1 px-md-0" id="contact">
            <div className="container px-2 mb-5">
                <div className="card px-2">
                    <div className="row">
                        <div className="col-md-6 p-4 p-md-5 contact_info">
                            <div className="p-2 p-md-0 pad-gap">
                                <h1 className="pt-3">CONTACT ME</h1>
                                <div className="d-flex justify-content-start align-items-center mt-5 fb">
                                    <i className="fa fa-facebook-f me-2 me-md-4"></i>
                                    <a href="https://www.facebook.com/dArkSidEbUg/">facebook.com/dArkSidEbUg</a>
                                </div>
                                <div className="d-flex justify-content-start align-items-center mt-4 gmail">
                                    <i className="fa fa-envelope me-2 me-md-4"></i>
                                    <a href="https://mail.google.com">nobeginmasob@gmail.com</a>
                                </div>
                                <div className="d-flex justify-content-start align-items-center mt-4 mobile mb-5">
                                    <i className="fa fa-mobile me-2 me-md-4"></i>
                                    <span>+639367653842</span>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6 p-4 p-md-5">
                            <div className="p-2 p-md-0">
                                <h3 className="pb-3">Leave me a message</h3>
                                <form className="emailForm" method="POST" onSubmit={sendEmail}>
                                    <div className="col-md-12 py-1">
                                        <label>Enter your full name:</label>
                                    </div>
                                    <div className="row">
                                        <div className="col-md-12">
                                            <input className="form-control mt-md-0" name="name" type="text" placeholder="Enter first name" required/>
                                        </div>
                                    </div>
                                    <div className="col-md-12 py-1 mt-3">
                                        <label>Enter your email:</label>
                                    </div>
                                    <div className="col-md-12 py-1">
                                        <input className="form-control mt-0" name="email" type="email" placeholder="Enter your email" required/>
                                    </div>
                                    <div className="col-md-12 py-1 mt-3">
                                        <label>Enter your message:</label>
                                    </div>
                                    <div className="col-md-12 py-1">
                                        <textarea className="form-control mt-0" rows="10" name="msg" placeholder="Write your message" required/>
                                    </div>
                                    <div className="col-md-12 py-1">
                                        <button type="submit" className="btn btn-lg px-4 float-end mt-4">Submit</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
