import React from 'react';

export default function Projects() {
    return (
        <div className="projects_section" id="projects">
            <div className="project-spacer">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 319">
                    <path fill="#f8a710" fill-opacity="1" d="M0,128L1440,288L1440,320L0,320Z"></path>
                </svg>
            </div>
            <div className="projects p-3 p-md-0">
                <div className="container pb-5 pb-md-4">
                    <h1 className="pt-5 pt-md-0">PROJECTS</h1>
                    <div className="row pt-3">
                        <div className="col-md-6 pt-4">
                            <div className="card p-4 border-warning my-4">
                                <h4><a href="https://github.com/darksidebug?tab=repositories">PWA-Automated Contact Tracing System</a></h4>
                                <p className="mb-0">A mobile based COVID-19 QR-Code scanner and tracer. This is a research and extension project of Southern Leyte State University - College of 
                                    Computer Studies and Information Technology in collaboration with the Municipality of Sogod. The system is capable of detecting if individual were subjected 
                                    for contact tracing or were tag under quarantine protocols during entry of in an establishments.
                                </p>
                            </div>
                            <div className="card p-4 border-warning my-4">
                                <h4><a href="https://github.com/darksidebug?tab=repositories">Macrohon Water System</a></h4>
                                <p>A research and extension project of Southern Leyte State University - College of Computer Studies and Information Technology for the Municipality of Macrohon.
                                    It has a powerful features such as it can manage consumer and system users, create and produce water works, add/edit water bill and add payment on bill, 
                                    generate consumer ledger, mobile based meter reading for field reader personnel, produce consumer water bill and etc.
                                </p>
                                <p className="mb-0">Team developed project</p>
                            </div>
                            <div className="card p-4 border-warning my-4">
                                <h4><a href="https://github.com/darksidebug?tab=repositories">CCSIT Room Key Monitoring with Faculty Class Locator and Monitoring</a></h4>
                                <p className="mb-0">A system for monitoring and borrowing of room keys integrated with micro controller for key locking. The system interconnected with faculty
                                    class locator and monitoring. The faculty's room location will be indicated in the electronic board based on the keys borrowed. The system 
                                    can set the availability of the faculty if he/she is on leave, travel, absent or on duty.
                                </p>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="card p-4 border-warning mb-4">
                                <h4><a href="https://github.com/darksidebug?tab=repositories">Payroll Management System</a></h4>
                                <p>A client project with a beautiful features. It is integrated with biometric fingerprint for time in/out. The system will automatically
                                    computes the salary of the employees based on their hours work, rate, late and deductions. The system can managed employee information, 
                                    work schedule and produced DTR, Pay Slip and Payroll and etc.
                                </p>
                                <p className="mb-0">Team developed project</p>
                            </div>
                            <div className="card p-4 border-warning my-4">
                                <h4><a href="https://github.com/darksidebug?tab=repositories">Biometric Fingerprint Time In/Out</a></h4>
                                <p>A client project, integrated to payroll management system. It has a powerful features such as it automatically computes the minutes late, 
                                    computes total hours worked, determine under time hours worked, computes overtime hours worked and manage employees biometric fingerprint and etc. 
                                </p>
                                <p className="mb-0">Team developed project</p>
                            </div>
                            <div className="card p-4 border-warning my-4">
                                <h4><a href="https://github.com/darksidebug?tab=repositories">GIS Based SLSU Asset Management System</a></h4>
                                <p className="mb-0">A client project for MSIT thesis, the system features includes such as managing of users, assets, transfer assets from one office 
                                    to another office and generate reports. This system integrated with building/office mapping. The system able to locate the assets location by 
                                    showing the pin on the building/office in the maps view. It can also determine how many assets within the building/office.
                                </p>
                            </div>
                            <a href="https://github.com/darksidebug?tab=repositories" className="btn btn-lg see-more mt-4 px-4">See more ...</a>
                        </div>
                    </div>
                </div>
            </div>
            <svg className="spacer" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1438 310">
                <path fill="#f8a710" fill-opacity="1" d="M0,128L1440,288L1440,320L0,320Z"></path>
            </svg>
        </div>
    )
}
