import React from 'react';

export default function Hero() {
    return (
        <div className="container hero_section" id="hero">
            <div className="row pt-md-5">
                <div className="col-sm-12 d-block d-md-none mb-2">
                    <div className="text-center">
                        <div className="logo">
                            <div className="d-inline"></div>
                            <div className="d-inline"></div>
                            <div className="d-inline"></div>
                            <div className="d-inline"></div> 
                        </div>
                    </div>
                </div>
                <div className="col-md-7 col-lg-5 pt-0 pt-md-4 pb-5 pb-md-0 mt-0 mt-md-3">
                    <div className="text-center text-lg-end">
                        <h1 className="benj">Hi, I'm Benj</h1>
                        <h2 className="skill">Software Developer</h2>
                        <a href="#contact" className="btn btn-warning btn-lg mt-4 px-4 hire-me">Hire Me </a>
                        <button type="button" className="btn bg-white btn-lg ms-2 mt-4 px-4 resume" >Resume</button>
                    </div>
                </div>
                <div className="col-md-5 col-lg-7">
                    <div align="center">
                        <div className="profile" >
                            <img className="mt-4" src={process.env.PUBLIC_URL + 'assets/img/AvatarMaker.png'} alt="Avatar" height="220" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
